.trendr {
  height: 100vh;
  width: 100vw;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  scroll-snap-align: start;
}

.trendr_container {
  width: 40vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.trendr_text {
  display: flex;
  width: 22.4vw;
  flex-direction: column;
  font-size: 30px;
  color: black;
  align-items: center;
  text-align: center;
  margin-top: 10%;
  margin-left: 25%;
}

.trendr_textDesc {
  margin-top: 3%;
  font-weight: 200;
  font-size: 20px;
  margin-left: 3%;
}

.trendr_buttons {
  margin-top: 0.5%;
  margin-left: 30%;
  display: flex;
  align-items: center;
}

p {
  margin-right: 10%;
}

.trendr_img {
  margin-top: 17.5%;
  margin-left: 110%;
  height: 51.5vh;
  width: 44.3vw;

  transform: rotate(-0.2deg);
}
